import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classes from './SandwichIngredient.css'

class SandwichIngredient extends Component {
    render() {
        let ingredient = null;

        switch (this.props.type) {
            case 'bread-bottom':
                ingredient = <div className={classes.BreadBottom} style={{
                    color: 'blue', height: '13%',
                    width: '80%',
                    background: 'linear-gradient(#F08E4A, #e27b36)'
                }} >testest</div>;
                break;
            case 'bread-top':
                ingredient = (
                    <div className={classes.BreadTop}>
                        <div className={classes.Seeds1}></div>
                        <div className={classes.Seeds2}></div>
                    </div>
                );
                break;
            case 'salami':
                ingredient = <div className={classes.Salami} />;
                break;
            case 'provolone-cheese':
                ingredient = <div className={classes.ProvoloneCheese} />;
                break;
            case 'prosciutto':
                ingredient = <div className={classes.Prosciutto} />;
                break;
            case 'salad':
                ingredient = <div className={classes.Salad} />;
                break;
            default:
                ingredient = null;
        }
        return ingredient;
    }
}

SandwichIngredient.propTypes = {
    type: PropTypes.string.isRequired
};


export default SandwichIngredient